<template>
<div>
    <nav-bar title="新增计划"></nav-bar>
    <div class="first tx-c animated slideInUp">
        <div class="content">
          <p class="name">
             <label>广告计划:</label>
             <input placeholder="请输入广告计划" type="text">
          </p>
          <p class="link">
            <label>目标链接:</label>
                <select>
                    <option value="http://">http://</option>
                    <option value="https://">https://</option>
                </select>
               <input placeholder="请输入链接地址" type="text">
           </p>
          <p>区域选择：</p>
          <p class="area">
              <a-rea @input="input"></a-rea>
          </p>
          <p class="trade">
              <label>行业选择：</label>
              <select v-model="trade">
                  <option value="">请选择</option>
                  <option v-for="(item,index) in trades" :value="item.name" :key="index">{{item.name}}</option>
              </select>
          </p>
          <p class="position-id">
             <label>行业版位:</label>
              <select v-model="positionValue">
                <option value="">请选择</option>
                <option v-for="(item, index) in positionList" :key="index" :value="item.type">{{item.title}}</option>
              </select>
          </p>
          <p>
            <label>出价方式</label>
            <select v-model="selectType">
              <option value="">CPC</option>
            </select>
          </p>
          <mt-button style="margin-top:20px;" type="primary" @click="next">下一步</mt-button>
        </div>
    </div>
</div>
</template>

<script>
import aRea from '@/components/common/area'
import navBar from '@/components/common/navBar'
export default {
  data () {
    return {
      selectType: '',
      areaObj: '',
      trade: '',
      trades: [],
      positionValue: '',
      positionList: []
    }
  },
  components: {
    aRea,
    navBar
  },
  mounted () {
    this.$api.requestCat().then(res => {
      if (res.success) {
        this.trades = res.data
      }
    })
    this.getPositionList()
  },
  methods: {
    getPositionList () {
      this.$api.requestadvP({type: 2}).then(res => {
        console.log(res)
        if (res.success) {
          this.positionList = res.data
        }
      })
    },
    next () {
      this.$router.replace('/buy/stepSecond')
    },
    input (areaObj) {
      this.areaObj = areaObj
      console.log(areaObj)
    }
  }
}
</script>

<style scoped lang="scss">
@import '../../assets/css/animate.scss';
.first{
    padding-top: 40px;
}
.content{
    width: 90%;
    margin: 0 auto;
    p{
        display:flex;
        align-items: center;
        justify-content: space-between;
         flex-wrap: wrap;
        line-height: 40px;
        font-size: 14px;
    }
}
label{
    width: 25%;
    text-align: left;
    font-size: 14px;
    white-space:nowrap;
}
input[type="text"]{
    width: 100%;
    padding: 4px 7px;
    border: 1px solid #ccc;
    font-size: 12px;
    border-radius:8px;
    &:focus{
        border: 1px solid #57a3f3;
    }
}
select{
    width:30%;
    height:22px;
    border: 1px solid #ccc;
    border-right: none;
    border-radius:5px;
    font-size: 12px;
}
.link label{
    width: 35%;
}
.trade{
    select{
        flex: 1;
    }
}
.position-id{
  select{
    width: 42%;
  }
}

</style>



