<template>
<div class="city-select">
  <select v-model="selectedProvince" name="province">
     <option value="">请选择</option>
     <option v-for="(item, index) in provinces" :value="item" :key="index">{{item.areaname}}</option>
  </select>
  <select v-model="selectedCity" name="city" v-if="selectedProvince">
     <option value="">请选择</option>
     <option v-for="(item, index) in cities" :value="item" :key="index">{{ item.areaname }}</option>
  </select>
  <select v-model="selectedBlock" name="block" v-if="selectedProvince&&selectedCity">
     <option value="">请选择</option>
     <option v-for="(item, index) in blocks" :value="item" :key="index">{{ item.areaname }}</option>
  </select>
</div>
</template>

<script>
export default {
  data () {
    return {
      selectedProvince: '',
      selectedCity: '',
      selectedBlock: '',
      provinces: [],
      cities: [],
      blocks: []
    }
  },
  computed: {
    info () {
      return {
        province: this.selectedProvince.areaname,
        city: this.selectedCity.areaname,
        block: this.selectedBlock.areaname
      }
    }
  },
  created () {
    this.$api.requestPr().then(res => {
      if (res.success) {
        this.provinces = res.data
      }
    })
  },
  methods: {
  },
  watch: {
    selectedProvince (newVal, oldVal) {
      if (newVal !== oldVal) {
        this.selectedCity = ''
        this.$api.requestGetA({pid: newVal.id}).then(res => {
          this.cities = res.data
        })
      }
    },
    selectedCity (newVal, oldVal) {
      if (newVal !== oldVal) {
        this.selectedBlock = ''
        this.$api.requestGetA({pid: newVal.id}).then(res => {
          this.blocks = res.data
        })
      }
    },
    selectedBlock (newVal, oldVal) {
      if (newVal !== oldVal) {
        this.$emit('input', this.info)
      }
    }
  }
}
</script>

<style scoped>
.city-select{
  width: 100%;
  display: flex;
}
select{
  width: 33.333%;
  font-size: 10px;
}
</style>



